$main-font: Vazirmatn RD UI FD, sans-serif;
$accent-color: #20c5ba;

/* RTL */

html,
body {
    direction: rtl;
}

/* custom styles */

img {
    opacity: 1 !important;
}

a {
    transition: all 150ms linear;
}

.post-card {
    .post-card-image {
        border-radius: 10px;
    }

    .post-card-title {
        direction: rtl;
        font-size: 3rem;
        font-family: $main-font;
        color: white;
    }

    .post-card-excerpt {
        direction: rtl;
        text-align: justify;
        font-family: $main-font;

        p {
            font-size: 1.5rem;
        }
    }
}

.post {
    .post-card-image {
        opacity: 0.9 !important;
        transition: opacity 200ms linear;

        &:hover {
            opacity: 1 !important;
        }
    }

    .author-name {
        font-family: $main-font;
    }
}
.gh-content {
    p {
        color: white;
        direction: rtl;
        text-align: justify;
        text-align-last: right;
        font-family: $main-font;
        font-size: 2rem;
        font-weight: 300;
        letter-spacing: 0.41px;

        a {
            text-decoration: none !important;
            transition: color 250ms;

            &:hover {
                color: $accent-color;
            }
        }

        strong {
            font-weight: 500;
            color: #91deda;
        }

        code {
            word-break: break-word;
            padding: 3px 5px 3px 5px;
            unicode-bidi: plaintext;
        }
    }

    ul,
    ol {
        font-family: $main-font;

        li {
            font-size: 16px;

            &::marker {
                color: $accent-color;
            }
        }
    }

    pre {
        direction: ltr !important;
        border-radius: 10px;
        background: #292d3e;
        border: none;
    }

    h2,
    h3,
    h4 {
        font-family: $main-font;
        color: $accent-color;
    }

    figure {
        img {
            border-radius: 10px;
        }

        .kg-bookmark-container {
            direction: ltr;
            border-radius: 10px;

            .kg-bookmark-title,
            .kg-bookmark-description {
                direction: rtl;
                font-family: $main-font;
            }
        }
    }

    blockquote {
        font-family: $main-font;
        font-size: 16px;
        color: #92a3ab;
        border-right: 3px solid $accent-color;
        border-left: 3px solid $accent-color;
    }

    img {
        max-width: 100%;
        border-radius: 10px;
    }

    video {
        max-width: 100%;
    }
}

.nav {
    li {
        a {
            font-family: $main-font;
        }
    }
}
